body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;letter-spacing:0.5px;line-height:1.5em}hr{margin-top:3em;margin-bottom:3em}button{cursor:pointer;padding:0.5em;border-radius:10px;background-color:#777;color:white;border:none;outline:none;box-shadow:0 3px #aaa;margin-top:0;-webkit-user-select:none;-ms-user-select:none;user-select:none;margin-bottom:0;transition:0.1s}button:hover{-webkit-filter:brightness(1.25);filter:brightness(1.25)}button:active{margin-top:3px;margin-bottom:-3px;box-shadow:0 0 #aaa}

.transmitter{height:100%;display:flex;flex-direction:column;justify-content:flex-end;align-items:center;margin-top:0.5em;margin-bottom:-0.5em}.transmitter .value{display:flex;flex-direction:row;justify-content:space-between;align-items:center;font-weight:bold;padding:20px;margin-bottom:20px;border-radius:20px;transform:translateY(25%);opacity:0;z-index:0;position:relative;transition:1s;border:2px solid #000}.transmitter .value:before{content:"";position:absolute;bottom:-15px;left:calc(50% - 15px);border-width:15px 15px 0;border-style:solid;border-color:#000 transparent;display:block;width:0}.transmitter .value:after{content:"";position:absolute;bottom:-12px;left:calc(50% - 12px);border-width:12px 12px 0;border-style:solid;display:block;width:0}.transmitter .value.counter{font-size:60px}.transmitter .value.show{transform:translateY(0);opacity:1}.transmitter .value .svg-inline--fa{padding-left:0.25em;font-size:40px}.transmitter .value .dialog{position:absolute;top:0;bottom:0;left:50%;transform:translateX(-50%);height:100%;width:100%}.transmitter .nextValue{opacity:0;transition:0s}.transmitter .nextValue.show{opacity:1;transition:1s}.transmitter .nextValue.openSesame{opacity:0}.transmitter .keyContainer .key{background:#333;padding:1em;border-top-left-radius:1.5em;border-top-right-radius:1.5em;display:flex;justify-content:space-between;width:100px}.transmitter .keyContainer .key .led{border-radius:50%;height:1em;width:1em;background-color:gray}.transmitter .keyContainer .key.pressed .led{background-color:red}.transmitter .keyContainer .key.pressed button{margin-top:3px;margin-bottom:-3px;box-shadow:0 0 #aaa}.pair .transmitter .value:after{border-color:#D5F5E3 transparent}.pair.kiosk .transmitter .value:after{border-color:#eee transparent}.pair .outOfRange .transmitter .value:after{border-color:#F9E79F transparent}

.receiver{text-align:center}.receiver .value{padding:0.25em;transition:1s}.receiver .value.valid{background:lightgreen;color:green}.receiver .status{padding:0.5em;border-width:2px}.receiver .status.Unlocked{border-style:dashed;border-color:green;color:green;background:lightgreen}.receiver .status.Locked{border-style:solid;border-color:gray;color:gray;background:#ddd}

.pair{background-color:#D5F5E3}.pair.kiosk{background-color:#eee}.pair .controlBar{padding:0.5em;display:flex;justify-content:space-between;border-bottom:1px solid gray;background-color:#eee}.pair .devices{display:flex;flex-direction:row}.pair .devices>div{padding:0.5em;transition:1s;transition-property:background-color;width:50%}.pair .devices>div:first-child{border-right:1px dashed gray}.pair .devices.outOfRange>div:first-child{border-right:3px solid black;background-color:#F9E79F}.pair .devices.outOfRange>div:last-child{background-color:#D6EAF8}@media only screen and (max-width: 1000px){.pair .devices{flex-direction:column-reverse}.pair .devices>div{width:auto}.pair .devices>div:first-child{border-top:1px dashed gray;border-right:none}.pair .devices.outOfRange>div:first-child{border-top:3px solid black;border-right:none}}

#app{height:100vh;overflow:auto}#app header{position:relative;height:50vh;background:#1d1d1f;color:white;padding:1em;display:flex;flex-direction:column;justify-content:center;align-items:center}#app header #github{position:absolute;top:0.5em;right:0.5em;color:white;font-size:2em}#app section,#app footer{padding:1em}#app footer{background:#ddd;border-top:1px solid gray;overflow:auto}#app .container{padding-left:15vw;padding-right:15vw}@media only screen and (max-width: 600px){#app .container{padding-left:1em;padding-right:1em}}#app #imgContainer{text-align:center}#app #imgContainer img{width:100%;max-width:500px}

